import * as yup from "yup";

const nicknameRegExp = /^[a-zA-Z--0-9]{1,30}$/;
const nameRegExp = /^[a-zA-Z--]{1,50}$/;

const step1Shema = yup
	.object()
	.shape({
		nickname: yup
			.string()
			.required("  ")
			.max(30, "   30 !")
			.matches(nicknameRegExp, " !"),
		name: yup
			.string()
			.required("  ")
			.max(50, "  50 !")
			.matches(nameRegExp, "  !"),
		sername: yup
			.string()
			.required("  ")
			.max(50, "  50 !")
			.matches(nameRegExp, "  !"),
		sex: yup.string().required("  "),
	})
	.required();

export default step1Shema;
